section.education {
    .resetmp;
    padding: 10px 0;

    .education-detail-card {
        padding-top: 50px;

        h3 {
            padding: 30px 20px 0 20px !important;
            text-align: center;
            position: relative;

            &::before {
                content: "";
                border: 1.5px solid @secondary-color;
                height: 22px;
                width: 22px;
                border-radius: 2px;
                transform: rotate(45deg) translateX(-50%);
                background-color: @primary-color;
                position: absolute;
                z-index: 1;
                left: 50%;
                top: 0;
                margin-top: 5px;
                -webkit-box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, 0.75);
                -moz-box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, 0.75);
                box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, 0.75);
            }

        }

        h4 {
            padding: 5px 20px 0 20px !important;
            text-align: center;
        }

        p {
            padding: 0 20px;
            text-align: center;
        }
    }
}

// Large devices (desktops, 992px and up)
@media (min-width: 992px) {
    section.education {
        position: relative;

        .education-detail-card {
            padding: 0;

            h3 {
                padding: 0 !important;
                text-align: left;

                &::before {
                    transform: rotate(45deg);
                    left: -48px;
                }

            }

            h4 {
                padding: 0 !important;
                text-align: left;
            }

            p {
                padding: 0;
                text-align: left;
                position: relative;

                &::before {
                    content: " ";
                    height: 250px;
                    width: 5px;
                    background-color: @secondary-color;
                    position: absolute;
                    left: -40px;
                    bottom: 5px;
                }
            }
        }
    }
}