aside.side-nav {
    background-color: @primary-color;

    .sideNav-wrap {
        display: flex;
        flex-direction: column;
        // justify-content: center;
        align-items: center;
        padding: 25px 0;
        font-family: "HKGrotesk-Medium";
        height: 100%;

        .profile-img {
            border: 8px solid white;
            background-color: @primary-color;
            width: 230px;
            border-radius: 50%;
            padding: 8px;

            .img-container {
                overflow: hidden;
                border-radius: 50%;

                img {
                    width: 100%;
                    transform: scale(1.5) translateY(10%);
                }
            }
        }

        h1.name {
            text-transform: uppercase;
            color: @secondary-color;
            font-size: 40px;
            text-align: center;
        }

        h3.job-banner {
            .resetmp;
            text-align: center;
            background-color: @secondary-color;
            color: white;
            text-transform: uppercase;
            letter-spacing: 1px;
            width: 100%;
            padding: 20px 0;
            font-size: 22px;
        }

        .contact-area {
            width: 100%;

            h1 {
                text-align: center;
                text-transform: uppercase;
                font-size: 40px;
                color: @secondary-color;
                padding: 10px 0;
            }

            .contact-bar {
                // display: inline;
                display: flex;
                flex-direction: row;
                align-items: center;
                width: 100%;
                margin: 15px 0;

                .icon-wrapper {
                    background-color: @secondary-color;
                    padding: 13px;
                }

                a {
                    .resetmp;
                    text-decoration: none;
                    text-align: center;
                    border: 2px solid @secondary-color;
                    color: @secondary-color;
                    border-left: none;
                    font-family: "HeronSans-Light";
                    font-size: 21px;
                    width: 100%;
                    padding: 7px 0;

                    &:hover {
                        background-color: #fff;
                    }
                }
            }
        }
    }
}


// Large devices (desktops, 992px and up)
@media (min-width: 992px) and (max-width: 1199.98px) {
    aside.side-nav {
        .sideNav-wrap {
            h1.name {
                font-size: 30px;
            }

            h3.job-banner {
                font-size: 18px;
            }

            .contact-area {
                h1 {
                    font-size: 30px;
                }

                .contact-bar {
                    a {
                        font-size: 18px;
                    }
                }
            }
        }
    }
}