body,
html {
    .resetmp;

    .comments-spaces {
        text-align: justify;
    }

    .remaining-contents span {
        display: none;
    }

    //  General / Common Css
    // General CSS required for RIGHT CV Section
    //  for Resume / CV Deatisl Sections

    main div.main {
        color: @secondary-color;

        p {
            font-family: "HeronSans-Light";
            font-size: 22px;
            color: #535353;
        }

        h2 {
            font-family: "HKGrotesk-Light";
            font-size: 25px;
            color: white;
            text-transform: uppercase;
        }

        h3 {
            font-family: "HKGrotesk-Medium";
            font-size: 28px;
            .resetmp;
        }

        h4 {
            font-family: "HeronSans-Light";
            font-size: 22px;
            text-transform: capitalize;
            .resetmp;
        }

        .ribbon {
            background-color: @secondary-color;
            padding: 0px 40px;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        .copyrights {
            text-align: center;
            font-size: 14px;
            padding: 0 10px;
            text-transform: uppercase;
        }
    }

}

@media (min-width:992px) {
    .copyrights {
        text-align: right !important;
    }
}