section.experience {
    .resetmp;
    padding: 20px 0;

    .experience-detail-card {
        padding-top: 50px;

        h3 {
            padding: 30px 20px 0 20px !important;
            text-align: center;
            position: relative;

            &::before {
                content: "";
                display: block;
                border: 1.5px solid @secondary-color;
                height: 22px;
                width: 22px;
                border-radius: 2px;
                transform: rotate(45deg) translateX(-50%);
                background-color: @primary-color;
                position: absolute;
                z-index: 1;
                left: 50%;
                top: 0;
                -webkit-box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, 0.75);
                -moz-box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, 0.75);
                box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, 0.75);
            }
        }

        h4 {
            padding: 5px 20px 0 20px !important;
            text-align: center;
        }

        p {
            padding: 0 20px;
            text-align: center;
        }
    }
}

// Large devices (desktops, 992px and up)
@media (min-width: 992px) {
    section.experience {
        position: relative;

        .experience-detail-card {
            padding-top: 20px;

            h3 {
                position: relative;
                padding: 0 !important;
                text-align: left;

                &::before {
                    transform: rotate(45deg);
                    left: -48px;
                    // left: -5.45%;
                    margin-top: 5px;
                }

            }

            h4 {
                padding: 0 !important;
                text-align: left;
                position: relative;

                &::before {
                    content: " ";
                    display: block;
                    height: 400px;
                    width: 5px;
                    background-color: @secondary-color;
                    position: absolute;
                    left: -40px;
                    bottom: -365px;
                }
            }

            p {
                padding: 0;
                text-align: left;
            }

            &:last-of-type {
                h4 {
                    &::before {
                        display: none;
                    }
                }
            }
        }
    }
}

// Large devices (desktops, 992px and up)
@media (min-width: 1199.98px) {
    section.experience {
        .experience-detail-card {
            h4 {
                &::before {
                    height: 350px;
                    bottom: -315px;
                }
            }
        }
    }
}

@media (min-width: 1199.86px) and (max-width: 1290px) {
    section.experience {
        .experience-detail-card {
            h4 {
                &::before {
                    height: 400px !important;
                    bottom: -370px !important;
                }
            }
        }
    }
}