section.summary {
    .resetmp;
    padding: 10px 0;

    p {
        padding: 0 20px;
        text-align: center;
    }
}

// Large devices (desktops, 992px and up)
@media (min-width: 992px) {
    section.summary {
        p {
            padding: 0;
            text-align: left;
        }
    }
}