section.hobbies {
    .resetmp;
    padding: 40px 0;

    .ribbon img {
        width: 40px;
    }

    .hobby-wrap {
        text-align: center;
        padding-top: 20px;

        .hobby-image {
            display: inline-block;
            background-color: @secondary-color;
            padding: 25px;
            border: 1px solid @primary-color;
            transition: all .3s ease;

            &:hover {
                background-color: @primary-color;
                border: 1px solid @secondary-color;

                img {
                    filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(271deg) brightness(100%) contrast(123%);
                }
            }
        }

        h2 {
            color: @secondary-color;
            font-family: "HKGrotesk-SemiBold";
            letter-spacing: 1px;
        }
    }
}